button {

 font-family: inherit;
 display: inline-block;
padding: 0px 10px;
 line-height: 2.5em;
 margin: 20px;
 position: relative;
 overflow: hidden;
 border: 2px solid #613432;
 transition: color .5s;
 z-index: 1;
 font-size: 17px;
 border-radius: 20px;
 font-weight: 500;
 color: #fff;
 background-color: #613432;
}

button:before {
 content: "";
 position: absolute;
 z-index: -1;
 background: red;
 height: 150px;
 width: 200px;
 border-radius: 50%;
}

button:hover {
 color: #fff;
}

button:before {
 top: 100%;
 left: 100%;
 transition: all .7s;
}

button:hover:before {
 top: -30px;
 left: -30px;
}

button:active:before {
 background: #3a0ca3;
 transition:  0s;
}
